#projects{
    min-height: 100vh;
    background: #ffbb99;
    .projects-container{
        padding: 20px;
        .project-item{
            display: flex;
            align-items: center;
            margin: 20px auto;
        }
        .project-time {
            font-size: 20px;
            color: white;
            margin-right: 10px;
            flex: 0.1;
        }
        .project-stack > img {
            width: 50px;
            height: 50px;
            border: 2px solid white;
            border-radius: 50%;
            flex: 0.2;
        }
        .project-hr-separator{
            width: 50px;
            height: 5px;
            background-color: white;
            flex: 0.1;
        }
        .project-details {
            font-size: 20px !important;
            color: white;
            border: 2px solid white;
            border-radius: 10px;
            padding: 10px;
            flex: 1.0;
        }
        .project-details > a{
            font-weight: bold;
            cursor: pointer;
            color: white !important;
            text-decoration: none;
        }
        .project-details > span:hover{
            text-decoration: underline;
        }
    }

    .loader-container{
        .loader {
        height: 20px;
        width: 250px;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        }
        .loader--dot {
        animation-name: loader;
        animation-timing-function: ease-in-out;
        animation-duration: 3s;
        animation-iteration-count: infinite;
        height: 20px;
        width: 20px;
        border-radius: 100%;
        background-color: black;
        position: absolute;
        /* border: 2px solid white; */
        }
        .loader--dot:first-child {
        background-color: #8cc759;
        animation-delay: 0.5s;
        }
        .loader--dot:nth-child(2) {
        background-color: #8c6daf;
        animation-delay: 0.4s;
        }
        .loader--dot:nth-child(3) {
        background-color: #ef5d74;
        animation-delay: 0.3s;
        }
        .loader--dot:nth-child(4) {
        background-color: #f9a74b;
        animation-delay: 0.2s;
        }
        .loader--dot:nth-child(5) {
        background-color: #60beeb;
        animation-delay: 0.1s;
        }
        .loader--dot:nth-child(6) {
        background-color: #fbef5a;
        animation-delay: 0s;
        }
        .loader--text {
        position: absolute;
        top: 200%;
        left: 0;
        right: 0;
        width: 4rem;
        margin: auto;
        }
        .loader--text:after {
        content: "Loading";
        font-weight: bold;
        animation-name: loading-text;
        animation-duration: 3s;
        animation-iteration-count: infinite;
        }
        
        @keyframes loader {
        15% {
            transform: translateX(0);
        }
        45% {
            transform: translateX(230px);
        }
        65% {
            transform: translateX(230px);
        }
        95% {
            transform: translateX(0);
        }
        }
        @keyframes loading-text {
        0% {
            content: "Loading";
        }
        25% {
            content: "Loading.";
        }
        50% {
            content: "Loading..";
        }
        75% {
            content: "Loading...";
        }
        }			          
    }
}