.education{min-height:100vh;background:#e9a1b9;padding:20px;display:flex;align-items:center;justify-content:center;flex-direction:column}.education .education_item{display:flex;align-items:center;justify-content:space-between}.education .education_item button{background:transparent;cursor:pointer;border:none;outline:none;font-size:30px;color:white}.education .education_item .info{color:white;text-align:center;flex:1 1}.education .education_item .info h1{font-family:'Monoton', cursive;font-size:70px;word-spacing:20px}.education .education_item .info .year{font-size:30px}.education .education_item .info .dest{font-size:40px;line-height:60px;font-family:'Press Start 2P', cursive;color:#808080}@media screen and (max-width: 990px){.education .education_item .info h1{font-size:50px}.education .education_item .info .dest{font-size:30px}}@media screen and (max-width: 550px){.education .education_item .info h1{font-size:40px}.education .education_item .info .dest{font-size:20px;line-height:30px}}

